html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-check {
  width: 1em;
  height: 1em;
  fill: #91DC5A;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

input {
  border: 1px solid grey;
  padding: 10px 15px;
}

.form-wrapper {
  margin: 15px auto;
  display: flex;
  justify-content: center;
}

.form-wrapper__input-wrap {
  position: relative;
  margin-bottom: 5px;
}

p.is-error {
  display: none;
}

p.is-valid {
  display: none;
}

.is-invalid > p.is-error {
  color: #f00;
  display: block;
  border: none;
  position: absolute;
  left: 105%;
  bottom: 25%;
  white-space: nowrap;
}

@media only screen and (max-width: 834px) {
  .is-invalid > p.is-error {
    position: static;
  }
}

.is-invalid > input {
  border: 1px solid #f00;
}

.is-valid > p.is-valid {
  color: #0f0;
  display: block;
  border: none;
  position: absolute;
  left: 105%;
  bottom: 25%;
}

.is-valid > input {
  border: 1px solid #0f0;
  position: relative;
}

.btn {
  position: relative;
  padding: 10px;
  border: 1px solid grey;
  outline: none;
  background: #fff;
  color: grey;
  text-transform: uppercase;
  cursor: pointer;
  z-index: 0;
  overflow: hidden;
  transition: all .5s ease .1s;
  width: 100%;
}

@media only screen and (max-width: 834px) {
  .btn {
    width: 66.5%;
  }
}

.btn:before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  z-index: -1;
  width: 0;
  height: 100%;
  background: #000;
  transition: all .5s ease .1s;
}

.btn:hover {
  color: #f00;
  border: 1px solid #f00;
}

.btn:hover:before {
  width: 100%;
}

.icon-check {
  fill: #0f0;
}
